.food-container{
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    gap:10px;
    margin-top: 50px;
  
  }
  .food-info{
      display: flex;
      flex-direction: column;
      align-items: center;
      height: auto;
      padding: 20px;
      border:1px solid grey;
      justify-content: center;
      border-radius: 5px;
      box-shadow: 0px 2px 4px rgba(0,0,0.2);
      width: 300px;
      transition: box-shadow 0.3s ease-in-out;
      cursor: pointer;
  }
  .food-info:hover{
      box-shadow: 0px 4px 8px rgba(0,0,0.2);
      transform:translatey(-2px);
  }
  .food-info img{
      width:300px;
      height: 300px;
  }
  p{
      color: red;
      /* margin-bottom: 100px; */
  }