.main{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 200px;
  }
  .service-box{
    display: flex;
    justify-content: space-between;
    background-color: antiquewhite;
    border: 1px solid #ccc;
    column-gap:20;
    border-radius: 5px;
    margin-top: 20px;
    margin-left:  100px;
    margin-right: 100px;
    box-shadow: 0 2px 4px rgba(0,0,0.3); 
    margin-bottom: 20px;
    
  }
  .service-box:hover{
    box-shadow: 0 4px 8px rgba(0,0,0.3); 
  }
  .service-items{
    display: flex;
    flex-direction: column;
    padding: 20px;
  
  }
  .service-items h1{
    color:"black";
  }
  .service-items p{
    color:grey;
    margin-top: -5px;
  
  }
  @media(max-width:768px){
  .service-items p{
    font-size: 12px;
  }
  }
    
  