*{
    font-family: "Poppins",sans-serif;
  }
  .main-about{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 150px;
  }
  .about-container{
    display: flex;
    flex-direction: column;
    margin-left: 100px;
  }
  .about-container h1{
    text-transform: uppercase;
    font-weight: 500;
  }
  .about-container p{
    color: black;
    font-size: 20px;
    text-align: justify;
    margin-right: 20px;
    margin-top: 50px;
  }
  .image{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .image img{
    width:400px;
    height: 400px;
    margin-right: 50px;
  }
  @media(max-width:768px)
  {
    .main-about{
      flex-direction: column;
    }
    .about-container{
      margin-left: 10px;
      margin-top: 10px;
    }
    .about-container h1{
      font-size: 20px;
      text-align: center;
    }
    .about-container p{
      font-size: 14px;
     text-align: justify;
     margin-left: 20px;
    }
    .image img{
      width:200px;
      height:200px;
    }
  }